import back from "../../components/back.vue";
export default {
	data() {
		return {
			pageName:'历史数据',
			pageNameFlag:1,
			backShowFlag:0,
			tabNum:1,
			terminalStatus:55,
			lineInfo:{
				lineName:'测试监测点'
			},
		}
	},
	methods: {
		scrollH(){
			var that =this;
			var sys = uni.getSystemInfoSync();
			var winWidth = sys.windowWidth;
			var winrate = 750/winWidth;
			var winHeight= sys.windowHeight;
			var statusBarHeight = sys.statusBarHeight;
			that.scrollHeight = parseInt((winHeight-statusBarHeight)*winrate-370)+'rpx';
		},
		changeTab(tabNum){
			this.tabNum=tabNum;
		},
		chooseLine(){
			var that=this;
			uni.navigateTo({
				url:'../chooseLine/chooseLine'
			});
			uni.$on('lineInfoSelect',function(data){
				that.lineInfo = data;
			});
			uni.$on('lineInfoQuery',function(data){
				that.lineInfo = data;
			})
		},
		jumpToDataIntegrityDetails(){
			uni.navigateTo({
				url:'../dataIntegrityDetails/dataIntegrityDetails'
			})
		},
		jumpToTerminalStatus(){
			uni.navigateTo({
				url:'../terminalStatus/terminalStatus'
			})
		},
		jumpToTerminalHistory(){
			var that=this;
			uni.navigateTo({
				url:'../terminalHistory/terminalHistory?lineInfo='+that.lineInfo
			})
		},
	},
	components: {
		back
	},
	onLoad() {
		var that = this;
		that.scrollH();
	}
}
